<template>
  <div>
    <h1 class="f16 b"></h1>
    <el-row style="padding-left:40px;">
      <p class="f16 b" style="width:50%;padding:10px 0;border-bottom:1px solid #eee;margin-bottom:20px;"></p>
      <el-col class="userImg">
        <el-upload :headers="headers" class="avatar-uploader" action="http://192.168.11.222:8080/servant/file" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-col>
      <el-form :model="userForm" :rules="userRules" ref="userForm" label-width="60px" class="settingPassword_form">
        <el-form-item label=":">
          <p>{{name}}</p>
        </el-form-item>
        <el-form-item label=":" prop="nickname">
          <el-input v-show="flag" type="tel" v-model="userForm.nickname" auto-complete="off" placeholder=""></el-input>
          <p v-show="!flag" style="display:inline-block;">{{nickName}}</p>
          <span v-show="!flag" style="border:1px solid #eee;background-color:#0089e3;padding:2px 5px;border-radius:5px;color:#fff;" @click="modfityName"></span>
          <span v-show="flag" style="border:1px solid #eee;background-color:#0089e3;padding:2px 5px;border-radius:5px;color:#fff;" @click="submitName"></span>
        </el-form-item>
        <el-form-item label=":">
          <p>{{name}}</p>
        </el-form-item>
      </el-form>
    </el-row>
  </div>
</template>

<style>
  .userImg h1 {
    font-size: 18px;
    font-weight: bold;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>

<script>
  import api from '../../axios/api.js'
 
  export default {
    data() {
      return {
        imageUrl: '',
        labelPosition: 'left',
        flag: false,
        userForm: {
          nickname: "",
        },
        userRules: {
          nickname: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
      };
    },
    created() {
      this.nickName = user['data']['nickname'];
      this.name = user['data']['name'];
    },
    computed: {
      headers() {
        return {
          'Authorization': 'Bearer ' + user['msg']
        }
      }
    },
    methods: {
      beforeUpload(file) {
        let fd = new Formdata()
        fd.append('key', file, 'fileName')
        //  
        return false // false
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
        console.log(this.imageUrl);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      modfityName() {
        this.flag = true;
      },
      submitName() {
        this.flag = false;
        var params = {
          "id":user['data']['id'],
          "nickname": this.userForm.nickname,
        }
        api.Post('/account/update', params)
          .then(res => {
          });
      }
    }
  }
</script>


