<template>
    <div>
        <h1 class="f16 b"></h1>
        <el-form :model="personalForm" :rules="personalRules" ref="personalForm" label-width="120px" class="settingPassword_form">
            <el-form-item label=":">
                <p>{{nickName}}</p>
            </el-form-item>
            <el-form-item label=":" prop="oldPass">
                <el-input type="password" placeholder="" v-model="personalForm.oldPass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="pass">
                <el-input type="password" placeholder="" v-model="personalForm.pass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="checkPass">
                <el-input type="password" placeholder="" v-model="personalForm.checkPass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="passStting('personalForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import api from "../../axios/api.js";
export default {
  data() {
    let validatePass = (rule, value, callback) => {
      let re = /^[A-Za-z0-9\!\@\#\$\%\^\&\*\(\)\_\+\`\~\-\=\,\.\;\:\<\>\?\|]{6,16}$/;
      if (
        value === "" ||
        !re.test(value) ||
        value.length < 6 ||
        value.length > 16
      ) {
        callback(new Error("6-16"));
      } else {
        if (this.personalForm.checkPass !== "") {
          this.$refs.personalForm.validateField("checkPass");
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      let re = /^[A-Za-z0-9\!\@\#\$\%\^\&\*\(\)\_\+\`\~\-\=\,\.\;\:\<\>\?\|]{6,16}$/;
      if (
        value === "" ||
        !re.test(value) ||
        value.length < 6 ||
        value.length > 16
      ) {
        callback(new Error("6-16"));
      } else if (value !== this.personalForm.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      nickName:'',
      personalForm: {
        oldPass: "",
        pass: "",
        checkPass: ""
      },
      personalRules: {
          oldPass: [
          {
            required: true,
            validator: validatePass,
            trigger: "blur"
          }
        ],
        pass: [
          {
            required: true,
            validator: validatePass,
            trigger: "blur"
          }
        ],
        checkPass: [
          {
            required: true,
            validator: validatePass2,
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
     
      
  },
  methods: {
    // 
    passStting(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let userId = window.localStorage.getItem('userId');
          var params = {
            id: userId,
            oldPass: this.personalForm.oldPass,
            newPass: this.personalForm.pass
          };
          let url='/account/modify';
          api.Put(url, params).then(res => {
            if (res["succ"] == true) {
              alert("");
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>



