<template>
    <div>
        <el-row :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
            <el-col :lg="10" :md="10" :sm="10" :xs="10" :offset="7" style="background-color:#fff;padding:30px 25px 15px 0">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

                    <el-form-item label="" prop="name">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="number">
                        <el-input v-model="ruleForm.number"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="email">
                        <el-input v-model="ruleForm.email"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="sex" >
                        <el-radio-group v-model="ruleForm.sex"style="width:50%;">
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                        </el-radio-group>
                        <el-button @click="submitForm('resumeForm')" style="background-color: #ddd;border:none;color:#fff;"></el-button>
                    </el-form-item>

                    <el-form-item>
                        <el-button @click="submitForm('ruleForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script type="text/ecmascript-6">
	export default {
		data() {
			return {
				ruleForm: {
					name: '',
					number: '',
					email: '',
					sex: '',
					manage: '',

				},
				rules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					number: [
						{ required: true, message: '', trigger: 'blur' }
					],
					email: [
						{ required: true, message: '', trigger: 'blur' }
					],
					sex: [
						{ required: true, message: '', trigger: 'change' }
					]
				}
			};
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						alert('submit!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
		}
	}
</script>

<style>

</style>


