import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../src/components/store/store'
import * as types from '../src/components/store/types'

import Main from '../src/Main.vue'
import Index from '../src/components/Index.vue'//
import Map from '../src/components/Map.vue'//
import Activitys from '../src/components/Activitys.vue'//
import ActivityPara from '../src/components/ActivityPara.vue'//
import article from '../src/components/article.vue'//
import Register from '../src/components/register/register.vue'//
import Login from '../src/components/register/login.vue'//
import ForgetPassword from '../src/components/register/forgetPassword.vue'//
import Talent from '../src/components/talent.vue'//
import TutorDetail from '../src/components/tutorDetail.vue'//
import TrainList from '../src/components/trainList.vue'//
import TutorList from '../src/components/tutorList.vue'//
import FinancingDetail from '../src/components/financingDetail.vue'//
import FinancingList from '../src/components/financingList.vue'//
import TopicList from '../src/components/topicList.vue'//
import splendid_list from '../src/components/splendid_list.vue'//
import service_class from '../src/components/service_class.vue'//
import personalCenter from '../src/components/personalCenter/personalCenter.vue';//
import demand_comm_service01 from '../src/components/personalCenter/demand_comm_service01.vue';//--
import demand_comm_service02 from '../src/components/personalCenter/demand_comm_service02.vue';//--
import demand_comm_service03 from '../src/components/personalCenter/demand_comm_service03.vue';//--
import demand_comm_service04 from '../src/components/personalCenter/demand_comm_service04.vue';//--
import demand_comm_service05 from '../src/components/personalCenter/demand_comm_service05.vue';//--
import demand_comm_service06 from '../src/components/personalCenter/demand_comm_service06.vue';//--
import personalInformation from '../src/components/personalCenter/personalInformation.vue';//--
import passwordSetting from '../src/components/personalCenter/passwordSetting.vue';//--
import service_detail from '../src/components/personalCenter/service_detail.vue';//--
//
import news from '../src/components/news.vue'
import incubators from '../src/components/incubators.vue'
import service from '../src/components/service.vue'
import financing from '../src/components/financing.vue'
import college from '../src/components/college.vue'
import station from '../src/components/station.vue'
import position from '../src/components/position.vue'
import school_detail from '../src/components/school_detail.vue'
import research from '../src/components/research.vue'
import incubators_details from '../src/components/incubators_details.vue'
import enter from '../src/components/enter.vue'
import train_Application from '../src/components/train_Application.vue'
import into from '../src/components/into.vue'
import need from '../src/components/need.vue'
import provider from '../src/components/provider.vue'
import question from '../src/components/question.vue'
//10.26
import train_detail from '../src/components/train_detail.vue'
import splendid_detail from '../src/components/splendid_detail.vue'
import activity_more from '../src/components/activity_more.vue'
import space_more from '../src/components/space_more.vue'
import incubator_more from '../src/components/incubator_more.vue'
//10.27
import into_more from '../src/components/into_more.vue'
import service_provider_more from '../src/components/service_provider_more.vue'
import invest_more from '../src/components/invest_more.vue'
import invest_detail from '../src/components/invest_detail.vue'
import financingDetail from '../src/components/financingDetail.vue'
//10.29
import problem from '../src/components/problem.vue'
import tutorProblem from '../src/components/tutorProblem.vue'
import service_needs from '../src/components/service_needs.vue'
//10.30
import attract from '../src/components/attract.vue'
import attract_college from '../src/components/attract_college.vue'
// import demand from '../src/components/demand.vue'
import financing_more from '../src/components/financing_more.vue'
import attract_detail from '../src/components/attract_detail.vue'
import demand_detail from '../src/components/demand_detail.vue'
import attract_list from '../src/components/attract_list.vue'
import demand_list from '../src/components/demand_list.vue'
//11.2
import office_list_window from '../src/components/office_list_window.vue'
import office_list_policy from '../src/components/office_list_policy.vue'
import office_list_detail from '../src/components/office_list_detail.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/index', component: Index, name: 'Index'
	},
	{
		path: '/map', component: Map, name: 'Map'},
	{ path: '/activitys', component: Activitys, name: 'Activitys' },
	{ path: '/activityPara/:id', component: ActivityPara, name: 'ActivityPara' },
	{ path: '/article/:id', component: article, name: 'article' },
	{ path: '/service', component: service, name: 'service' },
	{ path: '/register', component: Register, name: 'Register' },
	{ path: '/login', component: Login, name: 'Login' },
	{ path: '/forgetPassword', component: ForgetPassword, name: 'ForgetPassword' },
	{ path: '/talent', component: Talent, name: 'Talent' },
	{ path: '/trainList', component: TrainList, name: 'trainList' },//
	{ path: '/tutorDetail/:id', component: TutorDetail, name: 'tutorDetail' },//
	{ path: '/tutorList', component: TutorList, name: 'tutorList' },//
	{ path: '/financingDetail/:id', component: FinancingDetail, name: 'financingDetail' },//
	{ path: '/financingList', component: FinancingList, name: 'financingList' },//
	{ path: '/topicList', component: TopicList, name: 'topicList' },//
	{ path: '/service_class/:id', component: service_class, name: 'service_class' },//
	{
		path: '/personalCenter', component: personalCenter, name: 'personalCenter', children: [
			{ path: '/demand_comm_service01', component: demand_comm_service01, name: 'demand_comm_service01', hidden: true,meta: {
				requireAuth: true,
			} },
			{ path: '/demand_comm_service02', component: demand_comm_service02, name: 'demand_comm_service02' },
			{ path: '/demand_comm_service03', component: demand_comm_service03, name: 'demand_comm_service03' },
			{ path: '/demand_comm_service04', component: demand_comm_service04, name: 'demand_comm_service04' },
			{ path: '/demand_comm_service05', component: demand_comm_service05, name: 'demand_comm_service05' },
			{ path: '/demand_comm_service06', component: demand_comm_service06, name: 'demand_comm_service06' },
			{ path: '/personalInformation', component: personalInformation, name: 'personalInformation' },
			{ path: '/passwordSetting', component: passwordSetting, name: 'passwordSetting' },
			{ path: '/service_detail/:id', component: service_detail, name: 'service_detail' },
		]
	},
	//
	//  
	{ path: '/news', component: news, name: 'news' },//
	{ path: '/incubators', component: incubators, name: 'incubators' },//
	{ path: '/service', component: service, name: 'service' },//
	{ path: '/financing', component: financing, name: 'financing' },//
	{ path: '/research', component: research, name: 'research' },//
	{ path: '/station/:id', component: station, name: 'station' },//
	{ path: '/college', component: college, name: 'college' },//
	{ path: '/position', component: position, name: 'position' },//
	{ path: '/school_detail/:id', component: school_detail, name: 'school_detail' },//
	// {path: '/schoolList', component: schoolList, name: 'schoolList'},//
	{ path: '/incubators_details/:id', component: incubators_details, name: 'incubators_details' },//
	{
		path: '/enter/:id', component: enter, name: 'enter', meta: {
			requireAuth: true,
		} },//
	{ path: '/train_Application/:id', component: train_Application, name: 'train_Application' },//
	{ path: '/provider/:id', component: provider, name: 'provider' },//
	{ path: '/into/:id', component: into, name: 'into' },//
	{ path: '/need', component: need, name: 'need' },//
	{ path: '/question', component: question, name: 'question' },//
	//10.26
	{ path: '/train_detail/:id', component: train_detail, name: 'train_detail' },//
	{ path: '/splendid_detail/:id', component: splendid_detail, name: 'splendid_detail' },//
	{ path: '/splendid_list', component: splendid_list, name: 'splendid_list' },//
	{ path: '/activity_more', component: activity_more, name: 'activity_more' },//(more)
	{ path: '/space_more', component: space_more, name: 'space_more' },//(more)
	{ path: '/incubator_more', component: incubator_more, name: 'incubator_more' },//(more)
	//10.27
	{ path: '/invest_detail/:id', component: invest_detail, name: 'invest_detail' },//
	{ path: '/invest_more', component: invest_more, name: 'invest_more' },//(more)
	{ path: '/into_more', component: into_more, name: 'into_more' },//(more)
	{ path: '/service_provider_more', component: service_provider_more, name: 'service_provider_more' },//(more)
	{ path: '/financingDetail', component: financingDetail, name: 'financingDetail' },//
	//10.29
	{ path: '/problem/:type', component: problem, name: 'problem' },//
	{ path: '/tutorProblem/:businessId/:id', component: tutorProblem, name: 'tutorProblem' },//
	{ path: '/service_needs', component: service_needs, name: 'service_needs' },//
	//10.30
	{ path: '/attract', component: attract, name: 'attract' },//
	{ path: '/attract_college', component: attract_college, name: 'attract_college' },//
	// { path: '/demand', component: demand, name: 'demand' },//
	{ path: '/financing_more', component: financing_more, name: 'financing_more' },//(more)
	{ path: '/attract_detail/:id', component: attract_detail, name: 'attract_detail' },//
	{ path: '/demand_detail', component: demand_detail, name: 'demand_detail' },//
	{
		path: '/attract_list/:id', component: attract_list, name: 'attract_list', meta: {
			requireAuth: true,
		} },//
	{ path: '/demand_list', component: demand_list, name: 'demand_list' },//
	//11.2
	{ path: '/office_list_window', component: office_list_window, name: 'office_list_window' },//
	{ path: '/office_list_policy', component: office_list_policy, name: 'office_list_policy' },//
	{ path: '/office_list_detail/:id', component: office_list_detail, name: 'office_list_detail' },//
	// { path: '/financing_needs', component: financing_needs, name: 'financing_needs' },//
	{path: '*', //
		redirect: '/index'}

];
// token
if (window.localStorage.getItem('token')) {
	store.commit(types.LOGIN, window.localStorage.getItem('token'))
}
if (window.localStorage.getItem('userinfo')) {
	store.commit(types.USERINFO, window.localStorage.getItem('userinfo'))
}
const router = new VueRouter({
	linkActiveClass: 'active',
	mode: 'hash',
	base: __dirname,
	routes: routes
});
router.beforeEach((to, from, next) => {
	if (to.matched.some(r => r.meta.requireAuth)) {
		if (store.state.token) {
			next();
		}
		else {
			next({
				path: '/login',
				query: { redirect: to.fullPath }
			})
		}
	}
	else {
		next();
	}
})
export default router;



