<template>
    <div>
        <el-row :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
            <el-col :lg="14" :md="10" :sm="10" :xs="10" :offset="5" style="background-color:#fff;padding:30px 25px 15px 10px">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <h2></h2>
                    <el-form-item label="" prop="resource">
                        <el-radio-group v-model="ruleForm.resource">
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="" prop="region">
                        <el-select v-model="ruleForm.region" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <h2></h2>
                    <el-form-item label="" required>
                        <el-col :span="11">
                            <el-form-item prop="date1">
                                <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col class="line" :span="2">-</el-col>
                        <el-col :span="11">
                            <el-form-item prop="date2">
                                <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
                            </el-form-item>
                        </el-col>
                    </el-form-item>

                    <el-form-item label="" prop="name">
                        <el-col :span="11">
                            <el-form-item prop="date1">
                                <el-input type="date" v-model="ruleForm.date1" style="width: 100%;"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col class="line" :span="2"></el-col>
                        <el-col :span="11">
                            <el-form-item prop="date2">
                                <el-input type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-form-item>


                    <el-form-item label="" prop="name">
                        <el-col :span="10">
                            <el-form-item prop="date1">
                                <el-input type="date" v-model="ruleForm.date1" style="width: 100%;"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col class="line" :span="2">-</el-col>
                        <el-col :span="10">
                            <el-form-item prop="date1">
                                <el-input type="date" v-model="ruleForm.date1" style="width: 100%;"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col class="line" :span="2"></el-col>
                    </el-form-item>

                    <el-form-item label="" prop="name">
                        <el-col :span="7">
                            <el-form-item prop="date1">
                                <el-input type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item prop="date1">
                                <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="3" class="line">-</el-col>
                        <el-col :span="7">
                            <el-input type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-input>
                        </el-col>
                    </el-form-item>

                    <h2></h2>
                    <el-form-item label="" prop="name">
                        <el-input v-model="ruleForm.name" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="desc">
                        <el-input type="textarea" v-model="ruleForm.desc"></el-input>
                    </el-form-item>


                    <h2></h2>
                    <el-form-item label="" prop="name">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="name">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                    </el-form-item>

                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
	export default {
		data() {
			return {
				ruleForm: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				},
				rules: {
					name: [
						{ required: true, message: '', trigger: 'blur' },
						{ min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
					],
					region: [
						{ required: true, message: '', trigger: 'change' }
					],
					date1: [
						{ type: 'date', required: true, message: '', trigger: 'change' }
					],
					date2: [
						{ type: 'date', required: true, message: '', trigger: 'change' }
					],
					type: [
						{ type: 'array', required: true, message: '', trigger: 'change' }
					],
					resource: [
						{ required: true, message: '', trigger: 'change' }
					],
					desc: [
						{ required: true, message: '', trigger: 'blur' }
					]
				}
			};
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						alert('submit!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>
<style>
    h2{
        color:#20a0ff;
        font-size: 16px;
        font-weight: 600;
        margin:20px 10px;
    }
    .line{
        text-align: center;
    }
</style>


